/* Portfolio Section Styling */
.portfolio-section {
  background: var(--bg-color);
  padding: 8rem 5vw;
  text-align: center;
}

.portfolio-header h2 {
  font-size: 2.5rem;
  color: var(--text-color);
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.portfolio-header p {
  color: var(--subtext-color);
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto 3rem;
}

.portfolio-description p {
  color: var(--subtext-color);
  font-size: 1.2rem;
  max-width: 800px;
  margin: 0 auto 4rem;
  line-height: 1.7;
  text-align: center;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 3rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.project-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.07);
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: left;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.project-content {
  padding: 1.5rem;
}

.project-title {
  font-size: 1.6rem;
  color: var(--text-color);
  margin-bottom: 0.8rem;
  font-weight: bold;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  list-style: none;
  padding: 0;
  margin-bottom: 1rem;
}

.project-tags li {
  background: var(--primary-color);
  color: white;
  font-size: 0.9rem;
  padding: 0.3rem 0.7rem;
  border-radius: 20px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.project-description {
  font-size: 1.1rem;
  color: var(--subtext-color);
  margin-bottom: 1.2rem;
  line-height: 1.6;
}

.project-link {
  color: var(--accent-color);
  text-decoration: none;
  font-weight: 600;
}

.project-link:hover {
  text-decoration: underline;
}

/* Responsive for smaller screens */
@media (max-width: 768px) {
  .portfolio-header h2 {
    font-size: 2rem;
  }

  .portfolio-description p {
    font-size: 1rem;
    margin: 0 auto 3rem;
  }

  .portfolio-grid {
    grid-template-columns: 1fr;
  }
}
